import React from 'react';
import { Box, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';
import Image from 'next/image'; // Si usas Next.js, si no, usa 'img' directamente
import velasData from '../../velas.json'; // Importar el JSON

const ProductTable = () => {
  return (
    <Box mt={4}>
      <Button
        variant="contained"
        href="newProduct"
        sx={{
          backgroundColor: '#808080',
          color: 'white',
          '&:hover': {
            backgroundColor: '#616161',
          },
        }}
        size="large"
      >
        AÃ±adir Nuevo Producto
      </Button>

      <Button
        variant="outlined"
        sx={{
          color: '#808080',
          borderColor: '#808080',
          '&:hover': {
            borderColor: '#616161',
          },
          ml: 2,
        }}
        size="large"
      >
        Ver Pedidos
      </Button>

      {/* Tabla de productos */}
      <TableContainer component={Paper} sx={{ mt: 3 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Imagen</TableCell>
              <TableCell>Producto</TableCell>
              <TableCell>Cliente</TableCell>
              <TableCell>Total</TableCell>
              <TableCell>Fecha</TableCell>
              <TableCell>Estado</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {velasData.map((sale) => (
              <TableRow key={sale.id}>
                <TableCell>
                  <Image src={sale.imageUrl} alt={sale.productName} width={50} height={50} />
                </TableCell>
                <TableCell>{sale.productName}</TableCell>
                <TableCell>{sale.customerName}</TableCell>
                <TableCell>${sale.total}</TableCell>
                <TableCell>{sale.date}</TableCell>
                <TableCell>{sale.state}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default ProductTable;
